.textarea {

    box-sizing:border-box;
    padding: 20px;
    margin:10px 0;
    border:0;
    box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
    border-radius: 20px;
    width: 80%;
    background-color: #DDEEFF;
    margin-top: 40px;
}

.button {
    display: inline-block;
    color: white;
    text-decoration: none;
    padding: .3em 1.8em;
    outline: none;
    border-width: 2px 0;
    border-style: solid none;
    border-color: #2F6DE9 #000 #124ABA;
    border-radius: 20px;
    background: linear-gradient(#3470E6, #3470E6) #3470E6;
    transition: 0.2s;
    font-weight: bold;
    font-size: 15px;

}




